import { Product } from "../types/product"
import { ProductDTO } from "../types/ProductDTO"


const API_URL = 'http://localhost:8080/admin/products'
//handle get all product
export async function getAllProducts(): Promise<Product[]> {
  const res = await fetch(API_URL, {
    method: 'GET',
    headers: {
      'Content-Type': 'application/json',
    },
    next: { revalidate: 0 }, // Disable cache nếu cần real-time
  })

  if (!res.ok) {
    throw new Error('Lỗi khi tải danh sách sản phẩm')
  }

  return res.json()
}

//handle create product
export async function createProduct(product: ProductDTO): Promise<Product>{
  const res = await fetch(API_URL,{
    method: 'POST',
    headers:{
      'Content-Type': 'application/json',
    },
    body: JSON.stringify(product), // Gửi dữ liệu sản phẩm
    next: {revalidate:0}, // Disable cache để lấy dữ liệu real-time
  })
  if (!res.ok) {
    throw new Error('Lỗi khi tải danh sách sản phẩm')
  }

  return res.json()// Trả về một sản phẩm duy nhất
}